import { Component, OnInit } from '@angular/core';
import { Router, Event, NavigationEnd,ActivatedRoute } from '../../../../node_modules/@angular/router';
import { MainService } from '../../providers/main.service';
import * as $ from 'jquery'
declare var $: any
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
buttonColor:any;
  isLoggedIn: boolean;
  currUrl: string;
  route: string;
  name: any;
  profilePic: any;
 step:string;
  classAdd: any;
  constructor(public routes:Router , public service : MainService  ) {
    /*********** Routing Managed Start Here ***************/
  routes.events.subscribe( (event: Event) => {
    //console.log("events==>> ",event);
  if(event instanceof NavigationEnd) {
    this.currUrl = event.url.split('/')[1];
    this.currUrl = (this.currUrl.indexOf('?') != -1 ) ? this.currUrl.substr(0, this.currUrl.indexOf('?')) : this.currUrl
    console.log("current url ==>> "+this.currUrl)
   if(localStorage.getItem(`loginToken`) != null) {
   this.service.changeLoginSub(localStorage.loginToken)
    if((this.currUrl ==`` ||this.currUrl == `forgot` ||this.currUrl == `reset`)) {
        this.routes.navigate([`/home`])
          }
  } else {
    //console.log("else")
    if(!(this.currUrl == `/` ||this.currUrl == `reset`|| this.currUrl == `forgot`)) {
      this.routes.navigate([`/`])
      //console.log("else if");
    }
    this.service.changeLoginSub(localStorage.loginToken);
 }
 }
})
 /*********** Routing Managed End Here ***************/
   }

  ngOnInit() {
    
    this.step = window.location.pathname;
   // console.log("URL--->> ",this.step);
    this.name = localStorage.name;
    this.profilePic = localStorage.profilePic;
   // console.log('profilePics--->> ',this.profilePic);
    /*****************Header********************** */
    this.service.changeLoginSub(localStorage.login);
    this.service.loginObs.subscribe(response => {
    //  console.log('login observable response => '+ response)
      if(response != undefined) {
        this.isLoggedIn = true
   //  console.log("ISlogged======>>>>", this.isLoggedIn);
      }
      else {
        this.isLoggedIn = false
     // console.log("ISlogged======>>>>", this.isLoggedIn);
      }   
    })
    /*************************Image Profile****************/
    this.service.changeimageSub(localStorage.profilePic);
    this.service.imageObs.subscribe(response => {
     // console.log('Image observable response => '+ response)
      this.profilePic = response;
    })
  }

  signoutmodalFun(){
    console.log(`logout Modal`);
    $(`#signout_modal`).modal(`show`);
  }
  logout(){
    this.service.toastrSucc("Logout Successfully");
    localStorage.removeItem('id');
    localStorage.removeItem('name');
    localStorage.removeItem('profilePic');
    localStorage.removeItem('loginToken');
     this.routes.navigate([''])
  }
  /******************Router ******/
  active(){
    this.step = window.location.pathname;
  }
  /****************Script*************/
  script(){
    $(".btn-toggle,.close_panel").click(function() {
      $("body").toggleClass("toggle-wrapper");
  });
  }
}



