import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/main.service';

@Component({
  selector: 'app-myprofile',
  templateUrl: './myprofile.component.html',
  styleUrls: ['./myprofile.component.css']
})
export class MyprofileComponent implements OnInit {
  profileData: any= {};
  address: any;

  constructor(public service: MainService) { }

  ngOnInit() {
    this.profileApi();
  }
/**********************Profile Pic Api*****************/
profileApi(){
  this.service.spinnerShow()
  this.service.getApi(`/admin/myProfile`,1).subscribe(response => {
    // console.log(response)
    this.service.spinnerHide();
    if(response.body.responseCode == 200) {
      //this.service.toastrSucc(response.body.responseMessage);
      this.profileData = response.body.data;
      this.address = this.profileData.address;
      // console.log("address==> ",this.profileData.gender);
      // console.log("address==> ",this.address);
    } else {
      this.service.toastrErr(response.body.responseMessage);
      
    
    }
  }, error => {
    this.service.spinnerHide()
    this.service.toastrErr(`Something went wrong.`);
   
 })
}

}



