import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';
import { MainService } from '../../providers/main.service';
import { IMyDpOptions, IMyDateModel } from '../../../../node_modules/mydatepicker';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';

@Component({
  selector: 'app-wallet-managment',
  templateUrl: './wallet-managment.component.html',
  styleUrls: ['./wallet-managment.component.css']
})
export class WalletManagmentComponent implements OnInit {
  token: any;
  tab: any;
  token1Form:FormGroup;
  token2Form: FormGroup;
  token3Form: FormGroup;
  token4Form: FormGroup;
  tokenDetail: any;
  tokenList: any;
  beDisable: boolean=true; 
  p1: any = 1;
  walletDetail: any={};
  walletList: any=[];
  data: any={};
  

  constructor(public route: ActivatedRoute , public routes: Router, public service: MainService) { 
    
     /***************Form Validations ********************/
     this.token1Form = new FormGroup ({ 
       tokenname:new FormControl('',[]),
       startdate: new FormControl('',[]),
       enddate: new FormControl('',[]),
       });
    this.token2Form = new FormGroup ({ 
         tokenname:new FormControl('',[]),
         startdate: new FormControl('',[]),
         enddate: new FormControl('',[]),
         });
    }

  ngOnInit() {
    this.route.params.subscribe(params => {
      // console.log('params=>'+JSON.stringify(params));
      this.tab = params['tab'];
      this.tokenListApi();
      this.search('',this.p1);
      this.onDateChanged();
     });
   
    
  }

/**************** Date managing***************/
public myDatePickerOptions: IMyDpOptions = { 
  dateFormat: 'yyyy-mm-dd', 
  editableDateField:false, 
  openSelectorOnInputClick:false,
  disableSince: {year: 0, month: 0, day: 0}
  };
 public toDate: IMyDpOptions = { 
      dateFormat: 'yyyy-mm-dd', 
      editableDateField:false, 
      openSelectorOnInputClick:false,
      disableUntil: {year: 0, month: 0, day: 0}
      };

  onDateChanged() {
    let d = new Date();
    let copy1 = this.getCopyOfOptions();
    copy1.disableSince = {year: d.getFullYear(), 
    month: d.getMonth() + 1, 
    day: d.getDate()};
    this.myDatePickerOptions = copy1;
    }
 //Returns copy of myDatePickerOptions
getCopyOfOptions(): IMyDpOptions {
  return JSON.parse(JSON.stringify(this.myDatePickerOptions));
  }


public onChange(event : IMyDateModel){
  // console.log(this.transantionForm.value)
  // console.log(event)
  if(event.formatted) {
      this.beDisable = false
    let d: Date = new Date(event.jsdate.getTime());
    d.setDate(d.getDate() -  1);
    let copy: IMyDpOptions = this.getCopyOfToDateOpt();
    copy.disableUntil = {year: d.getFullYear(),
      month: d.getMonth() + 1,
      day: d.getDate()
    };
    this.toDate = copy;
  } else {
    this.beDisable = true
    

    this.token1Form.patchValue({
      'to': null,
      'from': null
    })
    
  }
  
}
getCopyOfToDateOpt(): IMyDpOptions {
  return JSON.parse(JSON.stringify(this.toDate));
}
/*******************Date managing Ends Here**************/
search(val,page){
  // console.log("val Value==>",val);
  this.data = val;
  // console.log("val Value==>",this.data);
   this.p1 = page;
  
    if(this.tab == 'sent'){
      var status = 'sent';
          }
    else
    {
    var status = 'receive';
    }
    var userData  = {
    "token":this.data.tokenname ? this.data.tokenname : null,
   "status":status,
    "fromDate": this.data.startdate ? Number(this.data.startdate.jsdate.getTime()): null,
    "toDate": this.data.enddate ? Number(this.data.enddate.jsdate.getTime()):null,
    'page':this.p1
    }
    // console.log("Data==> ",JSON.stringify(userData));
    this.service.spinnerShow()
    this.service.postApi(`/admin/walletManagement`, userData,1).subscribe(response => {
      // console.log(JSON.stringify(response))
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        if(status == 'sent'){
          this.token1Form.reset();
          this.walletDetail = response.body.data;
          this.walletList = this.walletDetail.docs;
          // console.log(`succces`,this.walletList);
        }
        else {
          this.token2Form.reset();
          this.walletDetail = response.body.data;
          this.walletList = this.walletDetail.docs;
          // console.log(`succces`,this.walletList);
        }
       }
       else {
       this.service.toastrErr(`Something went wrong`);
      }
    }, error => {
      
      this.service.spinnerHide()
      this.service.toastrErr(`Something went wrong`);
    })
}
/**************************Token List Api**************/
 tokenListApi(){
   var tokenData = {
       "tokenName": ''
       }  
       console.log("Search Token==> ",JSON.stringify(tokenData));
     this.service.spinnerShow()
     this.service.postApi(`/user/searchListToken`, tokenData,1).subscribe(response => {
      //  console.log(JSON.stringify(response))
      
       if(response.body.responseCode == 200) {
        this.service.spinnerHide()
          this.tokenDetail = response.body.data;
          this.tokenList = this.tokenDetail.docs;
          // console.log("Token List ====>>  ",this.tokenList)         
       }
        else {
        this.service.spinnerHide()
        this.service.toastrErr(`Something went wrong`);
       }
    }, error => {
     
     this.service.spinnerHide()
     this.service.toastrErr(`Something went wrong`);
    })
  }
}





