import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { routes } from './app.routing';
import { HomeComponent } from './pages/home/home.component';
import { HeaderComponent } from './pages/header/header.component';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http'; 
import { MainService } from './providers/main.service';
import { FormsModule, ReactiveFormsModule } from '../../node_modules/@angular/forms';
import { LoginComponent } from './pages/login/login.component';
import { ResetComponent } from './pages/reset/reset.component';
import { ForgotComponent } from './pages/forgot/forgot.component';
import { ChangeComponent } from './pages/change/change.component';
import { AddtokenComponent } from './pages/addtoken/addtoken.component';
import { ContentmanagmentComponent } from './pages/contentmanagment/contentmanagment.component';
import { EditprofileComponent } from './pages/editprofile/editprofile.component';
import { MyprofileComponent } from './pages/myprofile/myprofile.component';
import { StaticontentComponent } from './pages/staticontent/staticontent.component';
import { SweepComponent } from './pages/sweep/sweep.component';
import { TransactionmanagmentComponent } from './pages/transactionmanagment/transactionmanagment.component';
import { TransactionComponent } from './pages/transaction/transaction.component';
import { UsermanagmentComponent } from './pages/usermanagment/usermanagment.component';
import { ViewfeeComponent } from './pages/viewfee/viewfee.component';
import { WalletManagmentComponent } from './pages/wallet-managment/wallet-managment.component';
import { WithdrawlfeeComponent } from './pages/withdrawlfee/withdrawlfee.component';
import { MyDatePickerModule } from 'mydatepicker';
import { QRCodeModule } from 'angular2-qrcode';
import { CKEditorModule } from 'ngx-ckeditor';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastrModule } from 'ngx-toastr';
import { NgxSpinnerModule } from 'ngx-spinner';
import { TokenlistComponent } from './pages/tokenlist/tokenlist.component';
import {NgxPaginationModule} from 'ngx-pagination';
import { Ng4GeoautocompleteModule } from 'ng4-geoautocomplete';
@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    ReactiveFormsModule,
    MyDatePickerModule,
    QRCodeModule,
    NgxPaginationModule,
    CKEditorModule,
    RouterModule.forRoot(routes),
    BrowserAnimationsModule,
    ToastrModule.forRoot({
      preventDuplicates: true
    }),
    NgxSpinnerModule,
    Ng4GeoautocompleteModule.forRoot()
    //SlickModule.forRoot()
  ],
  declarations: [ AppComponent, 
    HomeComponent,
     HeaderComponent,
     LoginComponent,
     ResetComponent,
     ForgotComponent,
     ChangeComponent,
     AddtokenComponent,
     ContentmanagmentComponent,
     EditprofileComponent,
     MyprofileComponent,
     StaticontentComponent,
     SweepComponent,
     TransactionmanagmentComponent,
     TransactionComponent,
     UsermanagmentComponent,
     ViewfeeComponent,
     WalletManagmentComponent,
     WithdrawlfeeComponent,
     TokenlistComponent,
      ],
  bootstrap: [ AppComponent ],
  providers :[MainService]
  
})
export class AppModule {}


