import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';
import { MainService } from '../../providers/main.service';
declare var $ : any;
@Component({
  selector: 'app-contentmanagment',
  templateUrl: './contentmanagment.component.html',
  styleUrls: ['./contentmanagment.component.css']
})
export class ContentmanagmentComponent implements OnInit {
  public aboutValue: string = 'About';
  public termsValue: string = 'Terms';
  public privacyValue: string = 'Privacy';
  static: any;
  about: any=[];
  privacy: any={};
  terms: any=[];
  aboutid: any;
  termsid: any;
  privacyid: any;

  constructor(public route: ActivatedRoute , public routes: Router,public service: MainService) {
    this.route.params.subscribe(params => {
      //console.log('params=>'+JSON.stringify(params));
      this.static = params[`static`];
     
     });
   }

  ngOnInit() {
    this.staticFun();
  }
  /********************Static Function************/
  staticFun(){
    if(this.static == 'about'){
      var about = this.aboutValue;
      //console.log("About==> ",about);
      this.service.getApi(`/user/aboutUs`,1).subscribe(response => {
     //   console.log(JSON.stringify(response))
        
        if(response.body.responseCode == 200) {
          this.about = response.body.data[0].about;
          this.aboutid = response.body.data[0]._id;
         //  console.log('success',this.about);
        } else {
        //  console.log(`Something went wrong`);
        }
      }, error => {
       this.service.toastrErr(`Something went wrong`);
      })
    }
    else if(this.static == 'terms'){
      var terms = this.termsValue;
      //console.log("terms==> ",terms);
      this.service.getApi(`/user/termAndService`,1).subscribe(response => {
      //  console.log(JSON.stringify(response))
        
        if(response.body.responseCode == 200) {
          this.terms = response.body.data[0].terms;
          this.termsid = response.body.data[0]._id;
       // console.log('success',this.terms);
        } else {
         // console.log(`Something went wrong`);
        }
      }, error => {
       this.service.toastrErr(`Something went wrong`);
      })
    }
    else {
      var privacy = this.privacyValue;
      //console.log("privacy==> ",privacy);
      this.service.getApi(`/user/privacyPolicy`, 1).subscribe(response => {
        //console.log(JSON.stringify(response))
        
        if(response.body.responseCode == 200) {
          this.privacy = response.body.data.policy;
          this.privacyid = response.body.data._id;
          //console.log('success',this.privacy);
        } else {
          //console.log(`Something went wrong`);
        }
      }, error => {
        this.service.toastrErr(`Something went wrong`);
      })
    }
  }
  /****************Update Static Content***************/
  update(val){
    if(this.static == 'about'){
      var data =  {
        "_id":this.aboutid,
        "data":val
        }
    //  console.log("Dtat==> ",JSON.stringify(data));
     this.service.spinnerShow()
    this.service.postApi(`/admin/updateAboutUs`,data,1).subscribe(response => {
      // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
          this.staticFun();
        this.routes.navigate(['/static']);
      } else {
        this.service.toastrErr(response.body.responseMessage);
        // console.log(`Something went wrong`);
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr(`Something went wrong`);
      //console.log(`error`);
      
   })
    }
    else if(this.static == 'terms'){
      var data =  {
        "_id":this.termsid,
        "data":val
        }
    //  console.log("Dtat==> ",JSON.stringify(data));
     this.service.spinnerShow()
     this.service.postApi(`/admin/updateTermAndService`,data,1).subscribe(response => {
      // console.log(response)
       this.service.spinnerHide()
       if(response.body.responseCode == 200) {
        this.staticFun();
        this.routes.navigate(['/static']);
       } else {
         this.service.toastrErr(response.body.responseMessage);
        // console.log(`Something went wrong`);
       }
     }, error => {
       this.service.spinnerHide()
     this.service.toastrErr(`Something went wrong`);
       
    })
    }
    else {
      var data =  {
        "_id":this.privacyid,
        "data":val
        }
    //  console.log("Dtat==> ",JSON.stringify(data));
     this.service.spinnerShow()
    this.service.postApi(`/admin/updatePolicy`,data,1).subscribe(response => {
      // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        this.staticFun();
        this.routes.navigate(['/static']);
      } else {
        this.service.toastrErr(response.body.responseMessage);
        // console.log(`Something went wrong`);
      }
    }, error => {
      this.service.spinnerHide()
     this.service.toastrErr(`Something went wrong`);
      
   })
  }
}
script(){
  $('.jqte-test').jqte();

  // settings of status
  var jqteStatus = true;
  $(".status").click(function() {
    jqteStatus = jqteStatus ? false : true;
    $('.jqte-test').jqte({
      "status": jqteStatus
    })
  });
}
}



