import { Component, OnInit } from '@angular/core';
import { IMyDpOptions, IMyDateModel } from '../../../../node_modules/mydatepicker';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/main.service';
import { Router } from '../../../../node_modules/@angular/router';
 import * as jsPDF from 'jspdf';
declare var $ :any;
@Component({
  selector: 'app-usermanagment',
  templateUrl: './usermanagment.component.html',
  styleUrls: ['./usermanagment.component.css']
})

export class UsermanagmentComponent implements OnInit {
  userForm:FormGroup;
  userdetail: any=[];
  adminid: any;
  changeForm: FormGroup;
  userid: any;
  privateKey: any;
  address: any;
  blockval: number;
  p1:number = 1;
  ind: any;
  user: any={};
  beDisable: boolean=true;
  status: any;
  
  constructor(public service: MainService,public routes:Router) {
  
    /***********Form *************/
    this.userForm = new FormGroup ({ 
      startdate: new FormControl('', [Validators.required]),
      enddate: new FormControl('', [Validators.required]),
       });
    this.changeForm = new FormGroup ({ 
        password: new FormControl('', [Validators.required,Validators.minLength(8)]),
         confirmpassword: new FormControl('', [Validators.required,Validators.minLength(8)]),
          });
   }

  ngOnInit() {
   this.adminid = localStorage.id;
    this.onDateChanged();
    this.search('',1);
    //console.log("Block Val==>> ",this.blockval);
  }
/**************** Date managing***************/
public myDatePickerOptions: IMyDpOptions = { 
  dateFormat: 'yyyy-mm-dd', 
  editableDateField:false, 
  openSelectorOnInputClick:false,
  disableSince: {year: 0, month: 0, day: 0}
  };
 public toDate: IMyDpOptions = { 
      dateFormat: 'yyyy-mm-dd', 
      editableDateField:false, 
      openSelectorOnInputClick:false,
      disableUntil: {year: 0, month: 0, day: 0}
      };

  onDateChanged() {
    let d = new Date();
    let copy1 = this.getCopyOfOptions();
    copy1.disableSince = {year: d.getFullYear(), 
    month: d.getMonth() + 1, 
    day: d.getDate()};
    this.myDatePickerOptions = copy1;
    }
 //Returns copy of myDatePickerOptions
getCopyOfOptions(): IMyDpOptions {
  return JSON.parse(JSON.stringify(this.myDatePickerOptions));
  }


public onChange(event : IMyDateModel){
  // console.log(this.transantionForm.value)
  // console.log(event)
  if(event.formatted) {
      this.beDisable = false
    let d: Date = new Date(event.jsdate.getTime());
    d.setDate(d.getDate() -  1);
    let copy: IMyDpOptions = this.getCopyOfToDateOpt();
    copy.disableUntil = {year: d.getFullYear(),
      month: d.getMonth() + 1,
      day: d.getDate()
    };
    this.toDate = copy;
  } else {
    this.beDisable = true
    // this.filter(3)

    this.userForm.patchValue({
      'to': null,
      'from': null
    })
    
  }
  
}
getCopyOfToDateOpt(): IMyDpOptions {
  return JSON.parse(JSON.stringify(this.toDate));
}
/*******************Date managing Ends Here**************/
/***************Search Filter*************/
search(data,page){
  this.p1 = page;
  //console.log("Page Value",data);
  var userData  = {
    "fromDate": data.startdate ? data.startdate.jsdate.getTime(): null,
    "toDate": data.startdate ? data.enddate.jsdate.getTime(): null,
    'page':this.p1
    }
    //console.log("userdata==> ",JSON.stringify(userData));
    this.service.spinnerShow()
    this.service.postApi(`/admin/userManagement`,userData,1).subscribe(response => {
      //console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
       // this.service.toastrSucc(response.body.responseMessage);
        
        this.user =  response.body.data
        this.userdetail = this.user.docs;
        //console.log("data===> ",this.user);
        
      } else {
        this.service.toastrErr(response.body.responseMessage);
       
       //console.log(`Something went wrong`);
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr('Something went wrong');
             })
 
}
/****************Change Password Api**************/
change(id){
  this.userid = id;
  //console.log("user id==>",this.userid);
  $('#changepassword').modal({backdrop: 'static'});
}
changepasswordApi(data){
  var change ={
    "newPassword":data.password,
    "userId":this.userid, 
    "_id":this.adminid  
    }
    //console.log("DAta==> ",JSON.stringify(change));
    
    this.service.spinnerShow()
    this.service.postApi(`/admin/changePasswordOfUser`,change,1).subscribe(response => {
     // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        //this.service.toastrSucc(response.body.responseMessage);
        $('#changepassword').modal('hide');
        this.changeForm.reset();
        
      } else {
        this.service.toastrErr(response.body.responseMessage);
        $('#changepassword').modal('hide');
         this.changeForm.reset();
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr(error);
      $('#changepassword').modal('hide');
      this.changeForm.reset();
            })
}
/*****************Sweep***************/
sweepModal(id){
  this.userid = id;
  //console.log("user id==>",this.userid);
  
  var sweep ={
    "userId":this.userid, 
    "_id":this.adminid  
    }
    //console.log("DAta==> ",JSON.stringify(sweep));
    
    this.service.spinnerShow()
    this.service.postApi(`/admin/sweepPaperWallet`,sweep,1).subscribe(response => {
      //console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
       // this.service.toastrSucc(response.body.responseMessage);
          var sweepData = response.body.data;
          this.privateKey = sweepData.privateKey;
          this.address = sweepData.address;
      // console.log('sweep Data == > ',JSON.stringify(sweepData));
      // console.log('Private Key==>',this.privateKey);
      // console.log('address==> ',this.address);
        $('#sweep').modal({backdrop: 'static'});
       
        
      } else {
        this.service.toastrErr(response.body.responseMessage);
       
       
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr('Something went wrong');
      
      
            })
}
download(){
  //window.print();
  //console.log("Save Pdf");
  let doc = new jsPDF();
  doc.addHTML(document.getElementById("sweep"), function() {
     doc.save("Sweepdata.pdf");
  });
}
/******************Block & Unblock **************/

getuserid(id){
this.userid = id;
// this.ind = index;
  $('#block').modal({backdrop: 'static'});
}
getid(id){
  this.userid = id;
// this.ind = index;
// console.log("Index=>>" ,index);
$('#unblock').modal({backdrop: 'static'});
}
block(){  
     var blockdata =
     {
    "userId":this.userid,
    "_id":this.adminid,
    "status": "BLOCKED"
    }
    
   // console.log("Data==> ",JSON.stringify(blockdata));
    
    this.service.spinnerShow()
    this.service.postApi(`/admin/blockUserByAdmin`,blockdata,1).subscribe(response => {
     // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        //this.service.toastrSucc(response.body.responseMessage);
        //  this.status = response.body.data.status;
        $('#block').modal('hide');
        this.search(this.userForm.value, this.p1)
        // this.blockval = this.ind; 
        // console.log("bbb==>",this.blockval) ;
        // console.log("ind==> ",this.ind)   ;   
      } else {
        this.service.toastrErr(response.body.responseMessage);
        $('#block').modal('hide');
         
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr('Something went wrong');
      $('#block').modal('hide');
      
    })
  
  
}
unblock(){
  var blockdata = {
    "userId":this.userid,
    "_id":this.adminid,
    "status":"ACTIVE"
    }
    
    //console.log("Data==> ",JSON.stringify(blockdata));
    
    this.service.spinnerShow()
    this.service.postApi(`/admin/blockUserByAdmin`,blockdata,1).subscribe(response => {
     // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        //this.service.toastrSucc(response.body.responseMessage);
        // this.status = response.body.data.status;
        $('#unblock').modal('hide');
        this.search(this.userForm.value, this.p1)
        /* this.blockval = 1;
        console.log("bbb==>",this.blockval) ;
        console.log("ind==> ",this.ind)   ;   */ 
      } else {
        this.service.toastrErr(response.body.responseMessage);
        $('#unblock').modal('hide');
         
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr('Something went wrong');
      $('#unblock').modal('hide');
      
    })
}
}



