import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/main.service';
import { Router, ActivatedRoute } from '../../../../node_modules/@angular/router';

@Component({
  selector: 'app-reset',
  templateUrl: './reset.component.html',
  styleUrls: ['./reset.component.css']
})
export class ResetComponent implements OnInit {
  resetForm:FormGroup;
  id: any;
  ran: any;
  constructor(public service: MainService , public routes: Router, private activatedRoute: ActivatedRoute) {
    this.resetForm = new FormGroup ({ 
      password: new FormControl('', [Validators.required,Validators.minLength(8)]),
      confirmpassword: new FormControl('', [Validators.required,Validators.minLength(8)]),
      });
   }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(queryParams => {
        // console.log(queryParams)
        this.id = queryParams._id;
        this.ran = queryParams.ran;
    });
  }
/*************Reset Api********************/
resetApi(data){
  
  var url = '/admin/resetPassword?_id='+this.id+'&ran='+this.ran ; 
  // console.log("Reset==> ",url);
  var resetData ={
     "password":data.password
         }
  //  console.log("reset Data ====>>>> "+ JSON.stringify(resetData));
    this.service.spinnerShow();
    this.service.postApi(url,resetData,0).subscribe(response => {
      // console.log(response);
      this.service.spinnerHide();
      if(response.body.responseCode == 200) {
        this.service.toastrSucc(response.body.responseMessage);
         this.routes.navigate(['']);
      } else {
       this.service.toastrErr(response.body.responseMessage);
      }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr(`Something went wrong`);      
      })
}
}



