import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/main.service';
import { Router } from '../../../../node_modules/@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
loginForm:FormGroup;

  constructor(public service: MainService, public routes:Router) {
    this.loginForm = new FormGroup ({ 
      email: new FormControl('', [Validators.required,Validators.pattern(`[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$`)]),
      password: new FormControl('',Validators.required)
      });
   }

  ngOnInit() {
  }
/**********************Login Api****************/
loginApi(data){
  // console.log(data);
  var loginData ={
    "email":data.email,
    "password":data.password
}
//  localStorage.setItem('id','response.body.data._id');
//  this.routes.navigate(['/home']);
  // console.log("Login Data ====>>>> "+ JSON.stringify(loginData));
  this.service.spinnerShow()
    this.service.postApi(`/admin/login`,loginData,0).subscribe(response => {
      // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        this.service.toastrSucc(response.body.responseMessage);
        this.loginForm.reset();
        localStorage.setItem('id',response.body.data._id);
        localStorage.setItem('name',response.body.data.name);
        localStorage.setItem('profilePic',response.body.data.profilePic); 
        localStorage.setItem('loginToken',response.body.data.jwt);      
       this.routes.navigate(['/home']);
      } else {
        this.service.toastrErr(response.body.responseMessage);
      
      //  console.log(`Something went wrong`);
      }
    }, error => {
      this.service.spinnerHide()
      this.service.toastrErr(`Something went wrong`);
      
   })
}
}



