import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/main.service';
import { Router } from '../../../../node_modules/@angular/router';


declare var $:any;
@Component({
  selector: 'app-addtoken',
  templateUrl: './addtoken.component.html',
  styleUrls: ['./addtoken.component.css']
})
export class AddtokenComponent implements OnInit {
  
    addtokenForm:FormGroup;
  base64textString: any=['assets/img/token.png'];
  token64: any=['assets/img/token.png'];
  base64: string;
  tokenbase64: string;
  id: any;
  customForm: FormGroup;
  constructor(public service:MainService , public routes:Router, ) {
    this.addtokenForm = new FormGroup ({ 
        image: new FormControl('', [Validators.required]),
        name: new FormControl('', [Validators.required,Validators.pattern(/^[a-zA-Z\s]*$/)]),
        price:new FormControl('', [Validators.required,Validators.pattern(/^[0-9\s]*$/)]),
        decimal:new FormControl('', [Validators.required,Validators.pattern(/^[0-9\s]*$/)]),
        quantity:new FormControl('', [Validators.required,Validators.pattern(/^[0-9\s]*$/)]),
        contactaddress:new FormControl('', [Validators.required]),
         storageaddress : new FormControl('', [Validators.required]),
        privatekey : new FormControl('', [Validators.required]),
        });
        this.customForm = new FormGroup ({ 
          tokenimage: new FormControl('', [Validators.required]),
          tokencontactaddress: new FormControl('', [Validators.required]),
          //  storageaddress : new FormControl('', [Validators.required]),
          // privatekey : new FormControl('', [Validators.required]),
          });
   }

  ngOnInit() {
      this.id = localStorage.id  ;
  }
  ///////////////////// Image Conversion/////////////////////////////////
onUploadChange(evt: any) {
  this.base64textString = [] 
  // this.service.spinnerShow();
  const file = evt.target.files[0];
  
  if (file) {
    const reader = new FileReader();
    reader.onload = this.handleReaderLoaded.bind(this);
    reader.readAsBinaryString(file);
    // this.service.spinnerHide();
  }
}

handleReaderLoaded(e) {
  this.base64textString.push('data:image/png;base64,' + btoa(e.target.result)); 
  this.base64 = 'data:image/png;base64,' + btoa(e.target.result) ;
}
///////////////////// Image Conversion/////////////////////////////////
/****************** Custom Token Image *******************************/
onUploadChangeCustom(evt: any) {
  this.token64 = [] 
  // this.service.spinnerShow();
  const file = evt.target.files[0];
  
  if (file) {
    const reader = new FileReader();
    reader.onload = this.handleReaderLoadedCustom.bind(this);
    reader.readAsBinaryString(file);
    // this.service.spinnerHide();
  }
}

handleReaderLoadedCustom(e) {
  this.token64.push('data:image/png;base64,' + btoa(e.target.result)); 
  this.tokenbase64 = 'data:image/png;base64,' + btoa(e.target.result) ;
}
/******************* Custom Token Image End Here *********************/
/********************* Add Custom Token Function ***********************/
customTokenFun(formvalue){
console.log("Form Value ----> ",JSON.stringify(formvalue));
console.log("Image --<> ",this.tokenbase64);
var tokenData = {
  "contractAddress":formvalue.tokencontactaddress,
  "_id":this.id,
  "tokenType":"ERC",
  "tokenImage":this.tokenbase64
          }
console.log("Token Data ====>>>> "+ JSON.stringify(tokenData));
this.service.spinnerShow();
this.service.postApi(`/admin/addToken`,tokenData,1).subscribe(response => {
//  console.log(response)
 this.service.spinnerHide();
 if(response.body.responseCode == 200) {
   this.addtokenForm.reset();
   this.service.toastrSucc(response.body.responseMessage);
   $('#customToken').modal('hide');    
   this.token64 = ['assets/img/token.png'];
   this.routes.navigate(['/tokenList'])
 } else {
   this.token64 = ['assets/img/token.png'];
   this.service.toastrErr(response.body.responseMessage);
  
 }
}, error => {
 this.service.spinnerHide();
 this.token64 = ['assets/img/token.png'];
 this.service.toastrErr(`Something went wrong`);
 })
}
/********************* Add Custom Token Function ***********************/
/************************** Open Modal of Custom Token ****************/
addcustomToken(){
$('#customToken').modal('show');
}
/**************************** End Modal Function ***********************/
addtokenFun(val){
// console.log("Form Value==> ",JSON.stringify(val));
// console.log('image==>> ',this.base64);
var tokenData = {
    "tokenName":val.name,        
      "tokenImage":this.base64,
    "tokenPrice":val.price,
    "contractAddress":val.contactaddress,
    "quantity":val.quantity,
    "ownerAddress":val.storageaddress,
    "privateKey":val.privatekey,
    "tokenDecimal":val.decimal,
     "_id":this.id
      }


//console.log("Token Data ====>>>> "+ JSON.stringify(tokenData));
 this.service.spinnerShow();
  this.service.postApi(`/admin/addToken`,tokenData,1).subscribe(response => {
  //  console.log(response)
    this.service.spinnerHide();
    if(response.body.responseCode == 200) {
      this.addtokenForm.reset();
      this.service.toastrSucc(response.body.responseMessage);
    //  console.log(response.body.responseMessage);     
      this.base64textString = ['assets/img/token.png'];
      this.routes.navigate(['/tokenList'])
    } else {
      this.base64textString = ['assets/img/token.png'];
      this.service.toastrErr(response.body.responseMessage);
      }
  }, error => {
    this.service.spinnerHide();
    this.base64textString = ['assets/img/token.png'];
    this.service.toastrErr(`Something Went Wrong`);
    })
}
}



