import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/main.service';
import { Router } from '../../../../node_modules/@angular/router';

@Component({
  selector: 'app-forgot',
  templateUrl: './forgot.component.html',
  styleUrls: ['./forgot.component.css']
})
export class ForgotComponent implements OnInit {
  forgotForm:FormGroup;
  constructor(public service: MainService, public routes:Router) { 
    this.forgotForm = new FormGroup ({ 
      email: new FormControl('', [Validators.required,Validators.pattern(`[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$`)]),
         });
  }

  ngOnInit() {
  }
/*****************Forgot Form*******************/
forgotApi(val){
  // console.log("Data==>  ",val);
  var forgotData =  {
    "email":val.email
    }
// console.log("Forgot Data== >> ",JSON.stringify(forgotData));
this.service.spinnerShow();
this.service.postApi(`/admin/forgotPassword`,forgotData,0).subscribe(response => {
  // console.log(response)
  this.service.spinnerHide();
  if(response.body.responseCode == 200) {
  this.service.toastrSucc(response.body.responseMessage);
    this.forgotForm.reset();
     } else {
    this.service.toastrErr(response.body.responseMessage);
      
  }
}, error => {
  this.service.spinnerHide();
this.service.toastrErr(`Something went wrong`); 
  }) 

}
}



