import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Injectable()
export class MainService {
	
  //baseUrl:any=`http://172.16.21.33:1412`;
   baseUrl:any=`http://162.222.32.20:1412`;
  //baseUrl:any=`http://172.16.6.183:1412`;
  loginSub = new BehaviorSubject(``);
  loginObs = this.loginSub.asObservable();
  imageSub = new BehaviorSubject(``);
  imageObs = this.imageSub.asObservable();
  constructor(private http: HttpClient,private toastr: ToastrService, private spinner:NgxSpinnerService) {

   }
/*************Header Manage***************/
changeLoginSub(msg) {
	this.loginSub.next(localStorage.loginToken)
}
/*************Header Manage***************/
/*****************Image Profile*********************/
changeimageSub(msg) {
	this.imageSub.next(localStorage.profilePic)
}
/**************************************/
/**********Spinner Start*************/
spinnerShow() {
	this.spinner.show();
}
spinnerHide() {
	this.spinner.hide();
}
/***********Spinner Ends***************/
/***********Toaster Start**************/
toastrSucc(msg) {
	// console.log("Succcess Toast")
	this.toastr.success(msg);
}
toastrErr(msg) {
	// console.log("error Toast")
	this.toastr.error(msg);
}
/***********Toaster Ends****************/
   /********************* Post Api Structure ***************/
  postApi(url, data, isHeader): Observable<any> {
		var httpOptions;
		if(isHeader === 0) {
			httpOptions = {
				headers: new HttpHeaders({ "Content-Type": "application/json" }),
				observe: 'response'
			}
			return this.http.post((this.baseUrl+url), data, httpOptions)

		} else if(isHeader === 1) {
			httpOptions = {
				headers: new HttpHeaders({ "Content-Type": "application/json" ,"jwt":localStorage.getItem("loginToken")}),
				observe: 'response'
			}
			return this.http.post((this.baseUrl+url),data, httpOptions)

		}

  }
    /********************* Get Api Structure ***************/
  getApi(url, isHeader): Observable<any> {
		var httpOptions;
		if(isHeader === 0) {
			httpOptions = {
				headers: new HttpHeaders({ "Content-Type": "application/json" }),
				observe: 'response'
			}
			return this.http.get((this.baseUrl+url),httpOptions)

		} else if(isHeader === 1) {
			httpOptions = {
				headers: new HttpHeaders({ "Content-Type": "application/json" ,"jwt":localStorage.getItem("loginToken")}),
				observe: 'response'
			}
			return this.http.get((this.baseUrl+url), httpOptions)

		}

	}
	 /***************Script*************/
	 sidemenu(){
		$(".btn-toggle,.close_panel").click(function() {
		  $("body").toggleClass("toggle-wrapper");
	  });
	  }
	/*************Reload*************/
	// reload() {
    
	// 	if (window.localStorage) {
	// 	  console.log('localstorage....')
	// 	  if (!localStorage.getItem('firstLoad')) {
	// 		console.log('localstorage..ifffff..')
	// 		localStorage['firstLoad'] = true;
		   
	// 		window.location.reload();
			
	// 	  } else
	// 		localStorage.removeItem('firstLoad');
	// 	}
	//   }
}



