import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/main.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
declare var $:any;
@Component({
  selector: 'app-tokenlist',
  templateUrl: './tokenlist.component.html',
  styleUrls: ['./tokenlist.component.css']
})
export class TokenlistComponent implements OnInit {
  
  tokenList: any=[];
  p1:number=1;
  tokenid: any;
  detail: any={};
  limit: any;
  total: any;
  customForm: FormGroup;
  token64: any=['assets/img/token.png'];
  tokenbase64: string;
  id: any;
  constructor(public service: MainService , public route:Router) { 
    this.customForm = new FormGroup ({ 
      tokenimage: new FormControl('', [Validators.required]),
      tokencontactaddress: new FormControl('', [Validators.required]),
      //  storageaddress : new FormControl('', [Validators.required]),
      // privatekey : new FormControl('', [Validators.required]),
      });
  }

  ngOnInit() {
    this.tokenListApi(this.p1);
    this.id = localStorage.id  ;
  }
  /**************************Token List Api**************/
tokenListApi(page){
  this.p1 = page;
    var tokenData = {
        "tokenName": '',
        "page":this.p1
        }  
        // console.log("Search Token==> ",JSON.stringify(tokenData));
      this.service.spinnerShow()
      this.service.postApi(`/admin/tokenList`, tokenData,1).subscribe(response => {
       console.log(JSON.stringify(response))
        
        if(response.body.responseCode == 200) {
         this.service.spinnerHide()
           var tokenDetail = response.body.data;
          this.tokenList = tokenDetail.data;
          this.limit  = tokenDetail.limit;
          this.total = tokenDetail.total;
         console.log("Token List ====>>  ",this.tokenList)         
        }
         else {
         this.service.spinnerHide()
         this.service.toastrErr(`Something went wrong`);
        }
      }, error => {
         this.service.spinnerHide()
        this.service.toastrErr(`Something went wrong`);
      })
    }
/********************Delete****************/
/****************** Custom Token Image *******************************/
onUploadChangeCustom(evt: any) {
  this.token64 = [] 
  // this.service.spinnerShow();
  const file = evt.target.files[0];
  
  if (file) {
    const reader = new FileReader();
    reader.onload = this.handleReaderLoadedCustom.bind(this);
    reader.readAsBinaryString(file);
    // this.service.spinnerHide();
  }
}

handleReaderLoadedCustom(e) {
  this.token64.push('data:image/png;base64,' + btoa(e.target.result)); 
  this.tokenbase64 = 'data:image/png;base64,' + btoa(e.target.result) ;
}
/******************* Custom Token Image End Here *********************/
deletemodal(id){
  this.tokenid = id;
  // console.log("Token Id==> ",this.tokenid);
  $(`#delete`).modal({backdrop: 'static'});
}
delete(){
  var tokenid = {
    "_id":this.tokenid
    }
  // console.log("Search Token==> ",JSON.stringify(tokenid));
  this.service.spinnerShow()
  this.service.postApi(`/admin/deleteToken`,tokenid,1).subscribe(response => {
    // console.log(JSON.stringify(response))
    
    if(response.body.responseCode == 200) {
     this.service.spinnerHide();
     //this.service.toastrSucc(response.body.responseMessage);
     $(`#delete`).modal('hide');     
     this.tokenListApi(this.p1) ;
    }
     else {
     this.service.spinnerHide()
     this.service.toastrErr(`Something went wrong`);
    }
  }, error => {
   
    this.service.spinnerHide()
    this.service.toastrErr(`Something went wrong`);
  })
}

/********************View Modal ****************/
viewmodal(id){
  //$(`#view`).modal({backdrop: 'static'});
  this.tokenid = id;
  // console.log("Token Id==> ",this.tokenid);
  var tokenid = {
    "_id":this.tokenid
    }
    // console.log("Search Token==> ",JSON.stringify(tokenid));
  this.service.spinnerShow()
  this.service.postApi(`/admin/viewTokenInfo`,tokenid,1).subscribe(response => {
    console.log(JSON.stringify(response))    
    if(response.body.responseCode == 200) {
     this.service.spinnerHide();
     //this.service.toastrSucc(response.body.responseMessage);
     this.detail = response.body.data;
     console.log("TokenDetail==> ",this.detail) ; 
     $(`#view`).modal({backdrop: 'static'});     
    }
     else {
     this.service.spinnerHide()
     this.service.toastrErr(`Something went wrong`);
    }
  }, error => {
    this.service.spinnerHide()
    this.service.toastrErr(`Something went wrong`);
  })
}
/************************** Open Modal of Custom Token ****************/
addcustomToken(){
  $('#customToken').modal('show');
  }
  /**************************** End Modal Function ***********************/
  /********************* Add Custom Token Function ***********************/
customTokenFun(formvalue){
  console.log("Form Value ----> ",JSON.stringify(formvalue));
  console.log("Image --<> ",this.tokenbase64);
  var tokenData = {
    "contractAddress":formvalue.tokencontactaddress,
    "_id":this.id,
    "tokenType":"ERC",
    "tokenImage":this.tokenbase64
            }
  console.log("Token Data ====>>>> "+ JSON.stringify(tokenData));
  this.service.spinnerShow();
  this.service.postApi(`/admin/addToken`,tokenData,1).subscribe(response => {
  //  console.log(response)
   this.service.spinnerHide();
   if(response.body.responseCode == 200) {
     this.customForm.reset();
     this.service.toastrSucc(response.body.responseMessage);
     $('#customToken').modal('hide');    
     this.token64 = ['assets/img/token.png'];
     this.tokenListApi(this.p1);
   } else {
     this.token64 = ['assets/img/token.png'];
     this.service.toastrErr(response.body.responseMessage);
     $('#customToken').modal('hide');
     this.tokenListApi(this.p1);
   }
  }, error => {
   this.service.spinnerHide();
   this.token64 = ['assets/img/token.png'];
   this.service.toastrErr(`Something went wrong`);
   this.tokenListApi(this.p1);
   })
  }
  /********************* Add Custom Token Function ***********************/
}



