import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/main.service';
declare var $:any;
@Component({
  selector: 'app-withdrawlfee',
  templateUrl: './withdrawlfee.component.html',
  styleUrls: ['./withdrawlfee.component.css']
})
export class WithdrawlfeeComponent implements OnInit {
  withdrawForm: FormGroup;
  withdrawlist: any=[];
  withdrawfee: any;
  editForm: FormGroup;
  id: any;

  constructor(public service:MainService) { 
    this.withdrawForm = new FormGroup ({ 
      coinname: new FormControl('', [Validators.pattern( /^[^\s][a-zA-Z\s]*$/)]),
         });
    this.editForm = new FormGroup ({ 
          price: new FormControl('', [Validators.required,Validators.pattern(/^(?=.*[1-9])\d+(\.\d{1,2})?$/)]),
                   });
  }

  ngOnInit() {
    this.search('');
  }
  search(data){
    // console.log(data);
    var withdrawData ={
      "coinName":data ? data.coinname : '' 
  }
    // console.log("Withdraw Data ====>>>> "+ JSON.stringify(withdrawData));
    this.service.spinnerShow()
      this.service.postApi(`/admin/withDrawFee`,withdrawData,1).subscribe(response => {
        // console.log(response)
        this.service.spinnerHide()
        if(response.body.responseCode == 200) {
          //this.service.toastrSucc(response.body.responseMessage);
          this.withdrawlist = response.body.data;
          // console.log("withdraw==> ",this.withdrawlist);
        } else {
          this.service.toastrErr(response.body.responseMessage);
          //  console.log(`Something went wrong`);
        }
      }, error => {
        this.service.spinnerHide()
        this.service.toastrErr(`Something went wrong`);     
     })
  }
  /***********************Edit Fee************/
  edit(id){
// console.log('id==> ',id);
this.id = id
for(var i=0;i<this.withdrawlist.length;i++ ){
  if(id == this.withdrawlist[i]._id){
    this.withdrawfee = this.withdrawlist[i].withDrawFee;
    // console.log("withdraw==>> ",this.withdrawfee);
  }
}
$('#withdraw').modal({backdrop: 'static'});
  }

  editwithdraw(val){
   //console.log('Price==> '+val+"   "+this.id)
   var price = {
    "tokenId":this.id,
    "fee":val.price
    }
    // console.log("Val===>  ",JSON.stringify(price))
   this.service.spinnerShow()
   this.service.postApi(`/admin/editWithDrawFee`,price,1).subscribe(response => {
    //  console.log(response)
     this.service.spinnerHide()
     if(response.body.responseCode == 200) {
       //this.service.toastrSucc(response.body.responseMessage);
       this.search('');
       }
        else {
       this.service.toastrErr(`Something went wrong`);
            }
   }, error => {
      this.service.spinnerHide()
      this.service.toastrErr(`Something went wrong`);    
  })
  }
}



