import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '../../../../node_modules/@angular/router';
import { MainService } from '../../providers/main.service';
import { FormGroup, FormControl, Validators } from '../../../../node_modules/@angular/forms';
import { IMyDpOptions, IMyDateModel } from '../../../../node_modules/mydatepicker';
declare var $:any;
@Component({
  selector: 'app-transactionmanagment',
  templateUrl: './transactionmanagment.component.html',
  styleUrls: ['./transactionmanagment.component.css']
})
export class TransactionmanagmentComponent implements OnInit {
  token: any;
  tab: any;
  tab1Form: FormGroup;
  tab2Form: FormGroup;
  beDisable: boolean = true;
  p1:number=1;
  tokenDetail: any;
  tokenList: any;
  transactionDetail: any ={};
  transactionList: any =[];
  id: any;
  payData: any={};
  deci: any;
  hash: any;
  data: any={};
  constructor(public route: ActivatedRoute , public routes: Router , public service: MainService) {
   
     /**********Form Validation*************/
      this.tab1Form = new FormGroup ({ 
         tokenname:new FormControl('', [Validators.required]),
          startdate: new FormControl('', [Validators.required]),
          enddate: new FormControl('', [Validators.required]),
           });
      this.tab2Form = new FormGroup ({ 
               tokenname:new FormControl('', [Validators.required]),
                startdate: new FormControl('', [Validators.required]),
                enddate: new FormControl('', [Validators.required]),
                 });
   }

  ngOnInit() {
    this.route.params.subscribe(params => {
      // console.log('params=>'+JSON.stringify(params));
      this.tab = params['tab'];
      this.tokenListApi();
      this.search('',this.p1);
      this.onDateChanged();
     });
   
  }
  /**************** Date managing***************/
public myDatePickerOptions: IMyDpOptions = { 
  dateFormat: 'yyyy-mm-dd', 
  editableDateField:false, 
  openSelectorOnInputClick:false,
  disableSince: {year: 0, month: 0, day: 0}
  };
 public toDate: IMyDpOptions = { 
      dateFormat: 'yyyy-mm-dd', 
      editableDateField:false, 
      openSelectorOnInputClick:false,
      disableUntil: {year: 0, month: 0, day: 0}
      };

  onDateChanged() {
    let d = new Date();
    let copy1 = this.getCopyOfOptions();
    copy1.disableSince = {year: d.getFullYear(), 
    month: d.getMonth() + 1, 
    day: d.getDate()};
    this.myDatePickerOptions = copy1;
    }
 //Returns copy of myDatePickerOptions
getCopyOfOptions(): IMyDpOptions {
  return JSON.parse(JSON.stringify(this.myDatePickerOptions));
  }


public onChange(event : IMyDateModel){
  // console.log(this.transantionForm.value)
  // console.log(event)
  if(event.formatted) {
      this.beDisable = false
    let d: Date = new Date(event.jsdate.getTime());
    d.setDate(d.getDate() -  1);
    let copy: IMyDpOptions = this.getCopyOfToDateOpt();
    copy.disableUntil = {year: d.getFullYear(),
      month: d.getMonth() + 1,
      day: d.getDate()
    };
    this.toDate = copy;
  } else {
    this.beDisable = true
   

    this.tab1Form.patchValue({
      'to': null,
      'from': null
    })
    
  }
  
}
getCopyOfToDateOpt(): IMyDpOptions {
  return JSON.parse(JSON.stringify(this.toDate));
}
/*******************Date managing Ends Here**************/
/*******************Filter***********/
search(val,page){
  // console.log("Page Value==>",page);
   this.p1 = page;
   this.data = val
  //  console.log("Form Value==>",this.data);
    if(this.tab == 'Request'){
      var status = 'Request';
    }
    else
    {
    var status = 'Complete';
    }
    var userData  = {
   "tokenName":this.data.tokenname?this.data.tokenname:null,
   "action":status,
  "fromDate": this.data.startdate ? Number(this.data.startdate.jsdate.getTime()):null,
  "toDate": this.data.enddate ? Number(this.data.enddate.jsdate.getTime()):null,
    'page':this.p1
    }
    // console.log("Data==> ",JSON.stringify(userData));
    this.service.spinnerShow()
    this.service.postApi(`/admin/transactionDetails`, userData,1).subscribe(response => {
      // console.log(JSON.stringify(response))
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        if(this.tab == 'Request'){
          this.tab1Form.reset();
          this.transactionDetail = response.body.data;
          this.transactionList = this.transactionDetail.docs;
          // console.log(`succces`,this.transactionList);
        }
        else{
          this.tab2Form.reset();
          this.transactionDetail = response.body.data;
          this.transactionList = this.transactionDetail.docs;
          // console.log(`succces`,this.transactionList);
        }
       
      }
       else {
       this.service.toastrErr(`Something went wrong`);
      }
    }, error => {
      this.service.toastrErr('Something went wrong');
      this.service.spinnerHide()
      // console.log(`Error`)
    })
}
 /***************TokenListApi**************/
 tokenListApi(){
  var tokenData = {
      "tokenName": ''
      }  
      // console.log("Search Token==> ",JSON.stringify(tokenData));
    this.service.spinnerShow()
    this.service.postApi(`/user/searchListToken`, tokenData,1).subscribe(response => {
     //console.log(JSON.stringify(response))
     if(response.body.responseCode == 200) {
       this.service.spinnerHide()
         this.tokenDetail = response.body.data;
         this.tokenList = this.tokenDetail.docs;
        //  console.log("Token List ====>>  ",this.tokenList)         
      }
       else {
       this.service.spinnerHide()
       this.service.toastrErr(`Something went wrong`);
      }
   }, error => {
     ///this.service.toastrErr(JSON.stringify(error));
    this.service.spinnerHide()
    this.service.toastrErr(`Something went wrong`);
   })
 }
 /**************Payment****************/
 payment(id,deci){
   this.id = id;
   this.deci = deci;
// console.log("id====>> ",this.deci);
var paymentData ={
  "_id":this.id
  }
  // console.log("DAta==> ",JSON.stringify(paymentData));
   this.service.spinnerShow()
   this.service.postApi(`/admin/payInfo`,paymentData,1).subscribe(response => {
    // console.log(response)
    this.service.spinnerHide()
    if(response.body.responseCode == 200) {
      //this.service.toastrSucc(response.body.responseMessage);
      $('#payment').modal({backdrop: 'static'});
      this.payData = response.body.data;           
    }
     else {
      this.service.toastrErr(response.body.responseMessage);
     }
  }, error => {
    this.service.spinnerHide();
    this.service.toastrErr(`Something went wrong`);
    })

 }
 /**********pay Function*************/
 pay(){    
  var paymentData ={
    "_id_tokenPurchase":this.id,
    "tokenName":this.payData.tokenName,
    "toAddress":this.payData.userWalletAddress,
    "amount":this.payData.numberofToken,
    "tokenAddress":this.payData.tokenAddress,
    "decimal":2
    }
    
    // console.log("DAta==> ",JSON.stringify(paymentData));
     this.service.spinnerShow()
     this.service.postApi(`/admin/payToken`,paymentData,1).subscribe(response => {
      console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        //this.service.toastrSucc(response.body.responseMessage);
        this.hash = response.body.data;
        $('#payment').modal('hide');  
        $('#paysuccess').modal({backdrop: 'static'});
         this.search('',this.p1);          
      } else {
        this.service.toastrErr(response.body.responseMessage);
       }
    }, error => {
            this.service.spinnerHide();
            this.service.toastrErr(`Something went wrong`);
      })
  }
  update(){
    $('#paysuccess').modal('hide');

    this.search('',this.p1);
  }
 /************Reject***************/
 reject(id){
  this.id = id;
  // console.log("id====>> ",id);
  $('#rejected').modal({backdrop: 'static'});
 }
 /************ Confirm Reject***************/
 confirmreject(){
  var rejectData ={
       "_id_tokenPurchase":this.id  
    }
    // console.log("DAta==> ",JSON.stringify(rejectData));
    
    this.service.spinnerShow()
    this.service.postApi(`/admin/rejectRequest`,rejectData,1).subscribe(response => {
      // console.log(response)
      this.service.spinnerHide()
      if(response.body.responseCode == 200) {
        //this.service.toastrSucc(response.body.responseMessage);
        $('#rejected').modal('hide'); 
        this.search('',this.p1); 
        } 
        else 
        {
        this.service.toastrErr(response.body.responseMessage);
        }
    }, error => {
      this.service.spinnerHide();
      this.service.toastrErr(`Something went wrong`);
     
      })
 }
}



