import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/main.service';
import { Router } from '../../../../node_modules/@angular/router';

@Component({
  selector: 'app-staticontent',
  templateUrl: './staticontent.component.html',
  styleUrls: ['./staticontent.component.css']
})
export class StaticontentComponent implements OnInit {

  constructor(public service: MainService , public routes:Router) { }

  ngOnInit() {
  }
  /***********************About US**************/
aboutus()
{
  this.routes.navigate(['/content','about']);
}
 /***********************Terms**************/
 terms()
 {
  this.routes.navigate(['/content','terms']);
 }
  /***********************Privacy Policy**************/
privacy()
{
  this.routes.navigate(['/content','privacy']);
}
}



